body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: 800px;
  margin: 0 auto;
}
.tbl {
  width: 100%;
  border: 2px solid black;
  border-collapse: collapse;
}
.table_head {
  text-align: left;
  text-transform: uppercase; 
}
td, th {
  border: 1px solid black;
  padding: 15px; 
}

td:last-child {
  text-align: right;
}

th {
  background-color: #ccc;
}


.form_class > input{
  width: 100%;
  box-sizing: border-box;
  padding: .5rem;
}

.form_class > label {
  display: block;
  margin: 1rem 0 .3rem;
}

.btn_submit,  .btn_remove{
  color: #fff;
  padding: 10px 20px;
  text-transform: uppercase;
  border: none;
  border-radius: 5px;
}
.btn_submit:hover,  .btn_remove:hover {
  cursor: pointer;
  opacity: .8;
}
.btn_remove {
  background-color: rgb(195, 79, 58);
}

.btn_submit {
  margin-top: 1rem;
  height: 2.5rem;
  width: 100%;
  background-color: rgb(58, 111, 195);  
}